@import url("https://fonts.googleapis.com/css?family=Source+Sans+Pro:300,300i,400,400i,600,600i,700&display=swap");

* {
  font-family: "Source Sans Pro", sans-serif;
  margin: 0;
  padding: 0;
  text-decoration: none;
  list-style-type: none;
  outline: none;
}

html {
  font-size: 62.5%;
}

.App {
  display: flex;
  flex-direction: column;
  align-items: center;
  background: url('./meal-plan-bg.jpg') no-repeat center;
  background-size: cover;
  background-attachment: fixed;
  min-height: 100vh;
}


h1 {
  font-size: 7rem;
  letter-spacing: 0.1rem;
  font-style: italic;
  color: #000;
  text-shadow: 0 0.2rem 5rem rgb(119, 115, 115);
  text-transform: uppercase;
  margin: 4rem 0;
  text-align: center;
}

.search-form {
  width: 90%;
  height: 10rem;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #ffffffcf;
  border-radius: 0.5rem;
  margin-bottom: 5rem;
  box-shadow: 1rem 2rem 5rem #000;
  position: relative;
}

.alert {
  position: absolute;
  top: -3rem;
  left: 0;
  background-color: rgb(233, 72, 72);
  width: calc(100% - 0.4rem);
  border-radius: 0.5rem;
  padding: 0.2rem;
}

.alert h3 {
  font-size: 1.8rem;
  color: #fff;
  font-weight: 400;
  text-transform: uppercase;
  text-align: center;
}

.search-form input {
  font-size: 1.6rem;
  border: none;
  border-bottom: 0.1rem solid #ccc;
  margin: 0 0.5rem;
  border-radius: 0.5rem;
  height: 3rem;
}

.search-form input[type="text"] {
  padding: 0 0.5rem;
  width: 60%;
  color: #555;
}

.search-form input[type="submit"] {
  width: 25%;
  text-transform: uppercase;
  background-color: #000;
  color: #fff;
}

.recipes {
  width: 90%;
  display: flex;
  align-items: center;
  flex-wrap: wrap;
  justify-content: center;
  flex-direction: column;
}

.recipe {
  display: flex;
  flex-wrap: wrap;
  align-items: flex-start;
  justify-content: space-evenly;
  background-color: #ffffffcf;
  padding: 2rem;
  box-sizing: border-box;
  position: relative;
  border-radius: 0.5rem;
  box-shadow: 0 0 3rem rgb(0, 0, 0);
  width: 100%;
  margin-bottom: 20px;
}

.ingredients{
  max-width: 100%;
  width: 70%;
}
.image{
  max-width: 100%;
  width: 300px;
}

.recipe div:first-child{
  flex: 0 0 100%;
}

.recipe div:last-child{
  flex: 0 0 100%;
  text-align: right;

}

.recipe h2 {
  background-color: rgba(0, 0, 0, 0.5);
  border-radius: 0.5rem;
  padding: 0.3rem 0.5rem;
  text-align: center;
  font-size: 2.3rem;
  font-weight: 400;
  text-shadow: 0 0.5rem 0.5rem #555;
  color: #fff;
  margin-bottom: 20px;
}

.recipe img {
  border-radius: 0.5rem;
  object-fit: cover;
}

.recipe a {
  font-size: 1.8rem;
  color: #000;
  padding: 5px;
  text-decoration: underline;
}
.recipe a:hover{
  text-decoration: none;
}

.recipe p{
  padding: 5px;
  font-size: 1.2rem;
  text-align: center;
  position: absolute;
  top: 280px;
  left: 20px;
  right: 20px;
  background-color: rgba(0, 0, 0, 0.5);
  color: #fff;
  border-radius: 0.5rem;

}

.recipe button {
  font-size: 2rem;
  text-transform: uppercase;
  color: #fff;
  background-color: #000;
  border: none;
  border-radius: 0.5rem;
  height: 3rem;
  transition-duration: 0.3s;
  cursor: pointer;
  display: block;
  width: 100%;
}
.recipe button:hover{
  opacity: 0.9;
}
.ingredient-list {
  margin-bottom: 10px;
  background-color: #f0f0f0;
  border-radius: 0.5rem;
  padding: 0.5rem;
  width: 100%;
  display: flex;
  box-sizing: border-box;

}

.ingredient-list li {
  margin: 0.5rem 0;
  width: 100%;
}

.ingredient-text {
  font-size: 1.5rem;
  color: #888;
  flex: 0 0 65%;
}

.ingredient-weight {
  color: #000;
  font-size: 1.3rem;
  flex: 0 0 35%;
  text-align: right;
}
